    </OtherPageSlide>
    <v-container class="py-16">
      <v-row>
        <v-col v-for="(item,index) in items" :key="index+'tpitmes'" cols="12" md="3" >
          <div class="team-item"><img v-if="item.user.avatar" alt="pic" width="100%" class="team-img mx-auto"
                                      :src="$store.state.host + item.user.avatar">
            <img v-else alt="pic" width="100%" class="team-img mx-auto"
                 src="../assets/image/empty-photo.png">
            <div class="team-item-content">
              <v-tooltip
              color="black"
              v-if="$store.state.userId"
              top
              >
                <template v-slot:activator="{on,attrs}">
                  <div v-on="on" v-bind="attrs"  style="cursor: pointer" @click="toSeeProfile(item.user.id)" >
                    <h3  style="color: #fff" v-if="item.user && item.user.first_name && item.user.last_name" >{{ item.user.first_name }}
                      {{ item.user.last_name }}</h3>
                    <div class="team-info"><p>{{ item.post }}</p></div>
                  </div>

                </template>
                نمایش پروفایل
              </v-tooltip>
              <div v-else >
                <h3  style="color: #fff" v-if="item.user && item.user.first_name && item.user.last_name" >{{ item.user.first_name }}
                  {{ item.user.last_name }}</h3>
                <div class="team-info"><p>{{ item.post }}</p></div>
              </div>

              <ul class="team-icon">
                <li v-if="item.user.email" ><a class="pinterest" :href="'mailto: '+item.user.email" target="_blank">
                  <v-icon color="white" >mdi-email</v-icon> </a>
                </li>
                <li v-if="item.user.website_link" ><a class="pinterest" :href="'https://'+item.user.website_link" target="_blank">
