                </li>
                <li v-if="item.user.linkedin_link" ><a class="pinterest" :href="'https://instagram.com/'+item.user.linkedin_link" target="_blank">
                  <v-icon color="white" >mdi-instagram</v-icon> </a>
                </li>
                <li v-if="item.user.tel_link" ><a class="pinterest" :href="'https://t.me/'+item.user.tel_link" target="_blank">
                  <v-icon color="white" style="transform: rotate(-45deg)" >mdi-send-circle</v-icon> </a>
                </li>
              </ul>
            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
    <ProfileDialog :ddata="profileData" color="primary" />
  </div>
</template>

<script>
import OtherPageSlide from "@/components/Misc/OtherPageSlide";
import axios from "axios";
import ProfileDialog from "@/components/Misc/ProfileDialog";
export default {
  name: "TeamPage",
  components: {ProfileDialog, OtherPageSlide},
  created() {
    this.fetchTeam();
  },
  data () {
    return {
      profileData : {
        idSelected : null,
        dialog : false,
      },
      items : []
    }
  },
  methods : {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    fetchTeam(){
      axios.get(this.$store.state.host + '/accounts/api/v1/my-team/', ).then(response => {
        this.items = response.data.data;
      })
    }
  },
}
</script>

